<template>
    <div class="p-20">
        <div class="filter-container" style="margin-bottom: 20px">
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入会员登录名或UID" @keyup.native.enter="handleFilter" v-model="listQuery.loginName">
            </el-input>
            <span>日志类型</span>
            <el-select clearable style="width: 150px" class="filter-item" v-model="listQuery.operationType">
                <el-option v-for="log in userLogs" :key="log.key" :label="log.name" :value="log.key"></el-option>
            </el-select>
            <span>用户类型</span>
            <el-select clearable style="width: 150px" class="filter-item" v-model="listQuery.userType">
                <el-option v-for="userType in userTypeData" :key="userType.value" :label="userType.label" :value="userType.value"></el-option>
            </el-select>

            <el-button class="filter-item" @click="handleFilter" size="small" style="margin-left: 30px">查询</el-button>
        </div>
        <div>
            <el-table
                ref="singleTable"
                :data="tableData"
                v-loading="listLoading"
                element-loading-text="给我一点时间"
                style="width: 100%; ">
                <el-table-column
                  v-for="item in tableHeader"
                  :key="item.prop"
                  :label="item.label"
                  :prop="item.prop"
                  :width="item.width"
                  :min-width="item.minWidth"
                  :align="item.align"
                  :formatter="item.formatter"
                  show-overflow-tooltip>
                </el-table-column>
            </el-table>
            <div v-show="!listLoading" class="pagination">
                <el-pagination
                  @size-change="userPageSize"
                  @current-change="userPage"
                   :current-page="listQuery.page.pageNum"
                   :page-sizes="[15,30,45,60]" :page-size="listQuery.page.pageSize"
                   layout="total, sizes, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    /*import fetch from '@/utils/fetch'*/
    import DCTP from '@/utils/dctp';
    import {getrechargelist, useractionlog} from '@/api/useraddress';

    export default {
        data() {
            return {
                userLogs: [
                    {key: '', name: '全部'},
                    {key: 'dctp0000001', name: '修改/绑定邮箱成功'},
                    {key: 'dctp0000002', name: '修改/绑定邮箱失败'},
                    {key: 'dctp0000003', name: '修改/绑定手机号失败'},
                    {key: 'dctp0000004', name: '修改/绑定手机号成功'},
                    {key: 'dctp0000005', name: '修改登录密码成功'},
                    {key: 'dctp0000006', name: '修改登录密码失败'},
                    {key: 'dctp0000007', name: 'Google验证器绑定成功'},
                    {key: 'dctp0000008', name: 'Google验证器绑定失败'},
                    {key: 'dctp0000009', name: '设置/修改交易密码成功'},
                    {key: 'dctp0000010', name: '设置/修改交易密码失败'},
                    {key: 'dctp0000011', name: '实名认证申请失败'},
                    {key: 'dctp0000012', name: '实名认证申请成功'},
                    {key: 'dctp0000013', name: '设置/修改昵称成功'},
                    {key: 'dctp0000014', name: '设置/修改昵称失败'},
                    {key: 'dctp0000015', name: '会员登录'},
                    {key: 'dctp0000016', name: 'c2认证申请提交成功'},
                    {key: 'dctp0000017', name: 'c2认证申请提交失败'},
                    {key: 'dctp0000018', name: '视频认证申请提交成功'},
                    {key: 'dctp0000019', name: '视频认证申请提交失败'}
                ],
                //总条数
                total: null,
                //返回来列表的数据
                tableData: [],
                listLoading: true,

                userTypeData: [
                  { label : '用户端', value: 1 },
                  { label : '管理端', value: 2 }
                ],


                listQuery: {
                    loginName: '',
                    operationType: "",
                    userType: 1, // 1：C端(用户端)，2：B端(后台管理端)
                    page: {
                        pageNum: 1,
                        pageSize: 15
                    }
                },
                tableHeader: [
                    {
                        label: "用户ID",
                        prop: "uid",
                    },
                    {
                        label: "用户类型",
                        prop: "userType",
                        formatter(row, column, value) {
                          return value == 1 ? '用户端' : '管理端'
                        }
                    },
                     {
                        label: "登录名",
                        prop: "loginName",
                        formatter(row, column, value) {
                          return value
                        }
                    }, {
                        label: "IP地址",
                        prop: "ipAddress",
                    }, {
                        label: "操作内容",
                        prop: "actionContent",
                    },
                    {
                        label: "操作结果",
                        prop: "status",
                        formatter(row, column, value) {
                            return value == 1 ? '成功' : '失败'
                        }
                    },
                    {
                        label: "设备类型",
                        prop: "loginFrom",
                    },
                     {
                        label: "操作时间",
                        prop: "createTime",
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }
                ]
            }

        }
        ,
        computed: {
            tableDataLength() {
                return this.tableData.length;
            }
        }
        ,
        methods: {
            getList() {
                this.listLoading = true;
                useractionlog(this.listQuery).then(response => {
                    if (!response.result) {
                        this.msg = response.msg;
                        this.$message({
                            type: 'warning',
                            message: this.msg
                        });
                        this.listLoading = false;
                    } else {
                        this.tableData = response.data.data;
                        this.total = response.data.page.count;
                        this.listLoading = false;
                    }
                })
            },
            /**
             * 条件筛选查询对象
             */
            handleFilter() {
                this.getList();
            },
            //获取当前页数
            userPage(val) {
                this.listQuery.page.pageNum = val;
                this.getList();
            },
            //获取每页多少条
            userPageSize(val) {
                this.listQuery.page.pageSize = val;
                this.getList();
            }

        },
        created() {
            this.getList();
        }
    }
</script>



